	import uniLoadMore from '@/components/uni-load-more.vue';
	import uniNavBar from '@/components/uni-nav-bar.vue'
	import uniIcon from '@/components/uni-icon.vue'
	
	import { BASE_IMAGE_URL,getAllAddress } from '@/utils/api'
	
	export default {
		components: {
			uniLoadMore,
			uniNavBar,
			uniIcon
		},
		data() {
			return {
				loadingText: {
					contentdown: "上拉显示更多",
					contentrefresh: "正在加载...",
					contentnomore: "没有更多数据了"
				},
				list: [1,2,3,4,5,6,7,8,9,0,11,22,33,44],
				loadingType: 0,
				page: 1
			}
		},
		methods: {
			async getList(){
				let params = {
					userID: '',
					type: 1
				};
				let res = await getAllAddress(params);
				if(res.status == 1){
					
				}
				setTimeout(() => {
					uni.stopPullDownRefresh();
					this.list = this.list.concat([1,2,3,4,5,6,7,8,9,0,11,22,33,44]) ;
					if(this.list.length < 10){
						this.loadingType = 2;
					}else{
						this.loadingType = 0;
					}
				}, 1000);
			}
		},
		onReachBottom() {
			this.loadingType = 1;
			this.page++;
			this.getList();
		},
		
		onPullDownRefresh() {
			console.log("dddddd");
			this.page = 1;
			this.list = [];
			this.getList();
		},
		onLoad(){
			
		},
		onNavigationBarButtonTap:function(e){
            uni.navigateTo({
				url: '/pages/add_address/add_address'
			});
        },
	}
